<template>
  <div class="event-panel">
    <el-collapse v-model="activeCollapses">
      <template v-for="(event, index) in events">
        <el-collapse-item :key="event.eventName" :name="event.name">
          <template slot="title">
            <div class="title-wrap">
              {{ event.name }}（{{
                getEventHandlersByEventName(event.eventName).length
              }}）
              <span>
                <el-button
                  v-if="isShowEdit(event.eventName)"
                  type="text"
                  @click.stop="eidtEvent(event)"
                  >编辑({{ event.eventName }})</el-button
                >
                <el-button
                  v-if="isShowDelete(event.eventName)"
                  type="text"
                  style="color: red;"
                  @click.stop="deleteEvent(event, index)"
                  >删除</el-button
                >
              </span>
            </div>
          </template>
          <draggable
            v-model="eventStore[event.eventName]"
            forceFallback="true"
            :group="event.eventName"
            animation="300"
            handle=".el-icon-rank"
            @sort="handleSort"
          >
            <transition-group>
              <div
                class="handler"
                v-for="handler in getEventHandlersByEventName(event.eventName)"
                :key="handler.name"
              >
                <i class="icon el-icon-rank" />
                <el-tooltip
                  content="是否启用"
                  popper-class="tooltip-question"
                  placement="top"
                >
                  <el-switch
                    style="margin: 0 5px"
                    v-model="handler.disabled"
                    :active-value="false"
                    :inactive-value="true"
                    @change="handleDisableChange"
                  ></el-switch>
                </el-tooltip>
                <div
                  class="handler-name"
                  :class="{ disabled: handler.disabled }"
                  @click="handleEditHandler(handler, event)"
                >
                  {{ handler.name }}
                </div>
                <div
                  class="add-btn-primary"
                  @click="handleDeleteHandler(handler, event.eventName)"
                >
                  -
                </div>
              </div>
            </transition-group>
          </draggable>
          <div class="handler new">
            <div class="handler-name" @click="handleAddHandler(event)">
              <div class="add-btn-primary">+</div>
              新增事件处理行为
            </div>
          </div>
        </el-collapse-item>
      </template>
    </el-collapse>
    <div class="add-event" v-if="canAddEvent" @click="eidtEvent()">
      <el-button type="text">新增自定义事件</el-button>
    </div>
    <handler-dialog
      ref="handlerDialog"
      @confirm="handleUpdateEventData"
    ></handler-dialog>
    <add-event
      ref="addEvent"
      :events="events"
      :elementType="elementType"
      @confirm="addEventConfirm"
    ></add-event>
  </div>
</template>

<script>
  import draggable from 'vuedraggable'
  import { DESTROY, MOUNT } from '@/config/eventName'
  import { UPDATE_EVENTS_DATA } from '@/config/storeKey'
  import { getNodeByElementId, getRandomHexadecimal } from '@/utils/tree-utils'
  import { getConfig, getConfigByKey } from '@/renderer/tools/config'
  import { getGlobalConfig, update } from '@/renderer/tools/history'
  import _ from 'lodash'
  import { BIND_EVENTS, PROPS } from '@/config/eventKey'
  import EventBus, { globalEmit } from '@/renderer/tools/eventBus'
  import addEvent from './components/addEvent.vue'
  import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    name: 'EventPanel',
    components: {
      draggable,
      addEvent
    },
    inject: ['dndOptions', ...pageInject],
    data() {
      return {
        componentEvents: null,
        activeCollapses: [],
        MOUNT,
        DESTROY,
        collapses: [
          {
            name: MOUNT,
            title: '组件初始化',
            events: [],
          },
          {
            name: DESTROY,
            title: '组件销毁',
            events: [],
          },
        ],
        elementType: '',
        elementVersion: '',
        preSetEvents: [],
        canAddEvent: false
      }
    },
    computed: {
      elementId() {
        return this.dndOptions.selectedId
      },
      layoutData() {
        return this.getStore().layoutData
      },
      eventsData: {
        get() {
          return this.getStore().eventsData
        },
        set(n) {
          this._storeCommit(UPDATE_EVENTS_DATA, { eventsData: n })
        },
      },
      eventStore: {
        get() {
          return this.eventsData[this.dndOptions.selectedId] || {}
        },
      },
      events() {
        const innerEvents = this.componentEvents || []
        if (['timer'].includes(this.elementType)) {
          return innerEvents
        }
        return [
          {
            eventName: MOUNT,
            name: '组件初始化',
            condition: [],
            params: []
          },
          ...innerEvents,
          {
            eventName: DESTROY,
            name: '组件销毁',
            condition: [],
            params: []
          },
        ]
      },
    },
    watch: {
      'dndOptions.selectedId': {
        handler(n) {
          if (n) {
            setTimeout(() => {
              this.initEvents()
            }, 0)
          }
        },
        immediate: true,
      },
    },
    methods: {
      isShowEdit(eventName) {
        const { canAddEvent, elementType, elementVersion } = this
        const { events } = getConfig(elementType, elementVersion)
        return canAddEvent && ![MOUNT, DESTROY, ...events?.map(item => item.eventName)].includes(eventName) && 'antv-chart' !== elementType
      },
      isShowDelete(eventName) {
        const { canAddEvent } = this
        return canAddEvent && ![MOUNT, DESTROY, ...this.preSetEvents].includes(eventName)
      },
      // 新增/编辑自定义事件
      async addEventConfirm(formData, editIndex) {
        // 编辑
        if (editIndex !== -1) {
          // 编辑无改变, 不重复保存
          if(_.isEqual(this.events[editIndex], formData)) {
            return
          }
          const events = _.cloneDeep(this.events)

          events[editIndex] = formData
          this.componentEvents = _.cloneDeep(
            events.filter((item) => ![MOUNT, DESTROY, ...this.preSetEvents].includes(item.eventName))
          )
        }
        // 新增
        else {
          this.componentEvents.push(formData)
        }

        // 更新到store保存
        globalEmit.call(this, PROPS, {
          elementId: this.elementId,
          data: {
            eventsData: this.componentEvents.filter(item => !this.preSetEvents.includes(item.eventName)).map((item) => ({
              emitKey: item.eventName,
              emitParams: item.params,
              name: item.name,
            })),
          }
        })
      },
      async eidtEvent(event) {
        this.$refs.addEvent.open(event)
      },
      async deleteEvent(event, index) {
        const oldExtraVal = getGlobalConfig.call(this, [3, 4])
        const oldVal = _.cloneDeep(this.componentEvents)
        const hadSetEvent = this.eventStore[this.events[index].eventName]
        await this.$confirm(
          `确认要删除事件“${event.name}”?${hadSetEvent ? '\n此操作将会移除该事件下添加的所有事件处理行为!' : ''}`,
          '提示',
          {
            confirmButtonText: '确认',
            cancelButtonText: '取消',
            type: 'warning',
          }
        )
        if (hadSetEvent) {
          delete this.eventsData[this.elementId][this.events[index].eventName]
        }
        const trueIndex = this.componentEvents.findIndex(item => item.eventName === event.eventName)
        this.componentEvents.splice(trueIndex, 1)
        // 更新到store保存
        globalEmit.call(this, PROPS, {
          elementId: this.elementId,
          data: {
            eventsData: this.componentEvents.filter(item => !this.preSetEvents.includes(item.eventName)).map((item) => ({
              emitKey: item.eventName,
              emitParams: item.params,
              name: item.name,
            })),
          },
          pushHistory: false
        })
        // 记录历史栈
        update({
          elementId: this.elementId,
          oldVal,
          val: this.componentEvents,
          extra: {
            oldVal: oldExtraVal,
            val: getGlobalConfig.call(this, [3, 4]),
            deep: true
          },
          callback: val => {
            this.componentEvents = _.cloneDeep(val)
          }
        })
      },
      initEvents() {
        const { elementType, elementVersion } = getNodeByElementId(
          this.layoutData,
          this.elementId
        )
        this.preSetEvents = getConfig(elementType, elementVersion).events.map(item => item.eventName)
        this.elementType = elementType
        this.elementVersion = elementVersion
        this.canAddEvent = this.getCanAddEvent()
        this.componentEvents = getConfigByKey.call(this, {
          elementType,
          elementVersion,
          elementId: this.elementId,
        }).events
        this.activeCollapses = this.events
          .filter(
            (event) => this.getEventHandlersByEventName(event.eventName).length
          )
          .map((event) => event.name)
      },
      getCanAddEvent() {
        const { app, appUrl, moduleName, elementType } = this.getStore().dynamicProps[this.elementId] || {}
        if(elementType === 'business') {
          return (app || appUrl) && moduleName
        } else if(['page', 'antv-chart'].includes(elementType)) {
          return true
        }
        return false
      },
      getEventHandlersByEventName(eventName) {
        return this.eventStore[eventName] || []
      },
      handleAddHandler(event) {
        const eventName = event.eventName
        const id = getRandomHexadecimal(12)
        this.$refs.handlerDialog.open({
          id,
          elementId: this.dndOptions.selectedId,
          eventName,
          name: '',
          condition: [],
          emitEvent: {
            elementId: this.dndOptions.selectedId,
            eventName,
            params: event.params
          }
        })
      },
      handleEditHandler(handler, event) {
        const eventName = event.eventName
        this.$refs.handlerDialog.open({
          ...handler,
          eventName,
          elementId: this.dndOptions.selectedId,
          emitEvent: {
            elementId: this.dndOptions.selectedId,
            eventName,
            params: event.params
          }
        })
      },
      handleDeleteHandler(handler, eventName) {
        const events = this.getEventHandlersByEventName(eventName)
        const eventsData = {
          [this.dndOptions.selectedId]: {
            [eventName]: events.filter((n) => n.id !== handler.id),
          },
        }
        const oldEvents = getGlobalConfig.call(this, [3])
        this.eventsData = eventsData
        this.recordHistory(oldEvents)
      },
      handleUpdateEventData(res) {
        const events = this.getEventHandlersByEventName(res.eventName)
        const isAdd = !events.map((e) => e.id).includes(res.id)
        const eventsData = {
          [this.dndOptions.selectedId]: {
            ...this.eventsData[this.dndOptions.selectedId],
            [res.eventName]: isAdd
              ? [
                  ...events,
                  {
                    id: res.id,
                    functionId: res.functionId,
                    name: res.name,
                    binds: res.binds,
                    condition: res.condition,
                    action: res.action,
                    linkOptions: res.linkOptions,
                    controlOptions: res.controlOptions,
                    apiRequest: res.apiRequest,
                    sqlRequest: res.sqlRequest,
                    callback: res.callback,
                    loadingConfig: res.loadingConfig,
                    responseConfig: res.responseConfig,
                    pageEmit: res.pageEmit,
                    commonActionId: res.commonActionId
                  },
                ]
              : events.map((e) => {
                  if (e.id === res.id) {
                    return {
                      id: res.id,
                      functionId: res.functionId,
                      name: res.name,
                      binds: res.binds,
                      condition: res.condition,
                      action: res.action,
                      linkOptions: res.linkOptions,
                      controlOptions: res.controlOptions,
                      apiRequest: res.apiRequest,
                      sqlRequest: res.sqlRequest,
                      callback: res.callback,
                      loadingConfig: res.loadingConfig,
                      responseConfig: res.responseConfig,
                      pageEmit: res.pageEmit,
                      commonActionId: res.commonActionId
                    }
                  }
                  return e
                }),
          },
        }
        const oldEvents = getGlobalConfig.call(this, [3])
        this.eventsData = eventsData
        this.recordHistory(oldEvents)
      },
      // 事件处理行为修改记录历史记录,重做/撤销
      recordHistory(oldEvents) {
        update({
          elementId: this.elementId,
          extra: {
            oldVal: oldEvents,
            val: getGlobalConfig.call(this, [3]),
            deep: true
          }
        })
      },
      handleSort() {
      },
      handleDisableChange() {
        this._storeCommit(UPDATE_EVENTS_DATA, { eventsData: this.eventsData })
      }
    },
    mounted () {
      EventBus.$on(BIND_EVENTS, () => {
        // 为什么不用传进来的数据呢, 因为有些更改是只改变了个别数据,如果全替换,可能会导致某些字段缺失
        this.initEvents()
      })
    },
    beforeDestroy () {
      EventBus.$off(BIND_EVENTS)
    }
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';
  .event-panel::v-deep {
    width: 100%;
    padding: 0;
    .add-event {
      cursor: pointer;
      padding-right: 0;
      background: #f0f6ff;
      padding-left: 15px;
      border-top: 1px solid #dfe6ec;
      height: 30px;
      line-height: 30px;
      font-weight: 700;
    }
    .title-wrap {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .badge {
      display: block;
    }

    .el-collapse {
      border-bottom: none;
      border-left: none;
      border-right: none;
      .el-collapse-item__content {
        padding: 0;
      }
      .el-collapse-item__header {
        padding-right: 0;
        background: #f0f6ff;
        height: 30px;
        line-height: 30px;
        font-weight: 700;
      }
    }

    .handler {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding: 10px;
      font-size: 12px;

      &.new {
        background: #ffffff;
      }

      .icon {
        cursor: move;
        font-weight: bold;
        padding: 0 10px;
        font-size: 16px;
      }

      .handler-name {
        flex: 1;
        display: flex;
        align-items: center;

        &.disabled {
          color: #999999;
        }

        .add-btn-primary {
          margin-right: 10px;
        }

        &:hover {
          cursor: pointer;
          color: @drop-main-color;
        }
      }

      .delete-icon {
        color: red;

        &:hover {
          cursor: pointer;
        }
      }

      .add-icon {
        margin-right: 5px;
      }
    }
  }
</style>
