<template>
  <el-dialog
    custom-class="low-code-dialog"
    :title="editIndex !== -1 ? '编辑事件' : '新增事件'"
    :visible.sync="visible"
    :close-on-click-modal="false"
    width="450px"
  >
    <n-form label-position="left" ref="form" :model="formData" :rules="rules">
      <!-- antv chart 事件选择开始 -->
      <template v-if="isAntvChartMode">
        <n-form-item label="组件名" label-width="100px" prop="target">
          <el-select v-model="formData.target" placeholder="请选择组件名">
            <el-option
              v-for="item in targetOptions"
              :key="item.value"
              :label="`${item.label}(${item.value})`"
              :value="item.value">
            </el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="基础事件名" label-width="100px" prop="eventKey">
          <el-select v-model="formData.eventKey" placeholder="请选择基础事件名" @change="eventKeyChange">
            <el-option-group
              v-for="group in eventsOptions"
              :key="group.label"
              :label="group.label">
              <el-option
                v-for="item in group.options"
                :key="item.value"
                :label="`${item.label}(${item.value})`"
                :value="item.value">
              </el-option>
            </el-option-group>
          </el-select>
        </n-form-item>
      </template>
      <!-- 自定义名称开始 -->
      <template v-else>
        <n-form-item label="事件名" label-width="100px" prop="name">
          <el-input
            v-model.trim="formData.name"
            clearable
            placeholder="请输入事件名"
            style="width: 200px"
          ></el-input>
        </n-form-item>
        <n-form-item label-width="100px" prop="eventName">
          <span slot="label">
            事件key
            <el-tooltip
              :content="tip1"
              popper-class="tooltip-question"
              placement="top"
            >
              <i style="margin-left: 5px" class="el-icon-question"></i>
            </el-tooltip>
          </span>
          <el-input
            v-model.trim="formData.eventName"
            clearable
            placeholder="请输入事件key"
            style="width: 200px"
            :disabled="editIndex !== -1"
          ></el-input>
        </n-form-item>
      </template>
      <n-form-item>
        <span slot="label" style="z-index: 1; position: relative">
          参数设置
          <el-tooltip
            :content=" isAntvChartMode ? tip3 : tip2"
            popper-class="tooltip-question"
            placement="top"
          >
            <i style="margin-left: 5px" class="el-icon-question"></i>
          </el-tooltip>
        </span>
        <el-table
          :data="formData.params"
          border
          style="width: 100%"
          :span-method="arraySpanMethod"
        >
          <el-table-column fixed prop="date" label="参数描述">
            <template slot-scope="scope">
              <div style="padding-bottom: 10px">
                <n-form-item
                  :prop="'params.' + scope.$index + '.name'"
                  :rules="paramsRules"
                >
                  <el-input v-model.trim="scope.row.name" clearable :disabled="isAntvChartMode"></el-input>
                </n-form-item>
              </div>
            </template>
          </el-table-column>
          <el-table-column v-if="!isAntvChartMode" label="操作">
            <template slot-scope="scope">
              <div style="padding-bottom: 22px">
                <el-button
                  type="text"
                  style="color: red"
                  size="small"
                  @click="formData.params.splice(scope.$index, 1)"
                  >删除</el-button
                >
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div v-if="!isAntvChartMode" class="add-btn" @click="addParams">新增参数</div>
      </n-form-item>
    </n-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取 消</el-button>
      <el-button type="primary" @click="confirm">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
import _ from 'lodash'
import { Table, TableColumn } from 'element-ui'
import { PAGE_COMMON_EVENTS } from '@/config/eventName'
import { targetOptions, eventsOptions } from './antv-events'
export default {
  components: {
    ElTable: Table,
    ElTableColumn: TableColumn,
  },
  props: {
    events: {
      type: Object,
      required: true,
    },
    elementType: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      visible: false,
      rules: {
        name: [
          {
            required: true,
            trigger: 'change',
            validator: (rule, val, callback) => {
              if (!val) {
                callback(new Error('请输入事件名'))
              } else {
                const index = this.events.findIndex((item) => item.name === val)
                if (index !== -1 && index !== this.editIndex) {
                  callback(new Error(`事件名“${val}”已存在`))
                } else {
                  callback()
                }
              }
            },
          },
        ],
        eventName: [
          {
            required: true,
            trigger: 'change',
            validator: (rule, val, callback) => {
              if (!val) {
                callback(new Error('请输入事件key'))
              } else {
                const index = this.events.concat({ eventName: PAGE_COMMON_EVENTS }).findIndex(
                  (item) => item.eventName === val
                )
                if (index !== -1 && index !== this.editIndex) {
                  callback(new Error(`事件key“${val}”已存在`))
                } else {
                  callback()
                }
              }
            },
          },
        ],
        target: [
          {
            required: true,
            trigger: 'change',
            message: '请选择组件名'
          },
        ],
        eventKey: [
          {
            required: true,
            trigger: 'change',
            validator: (rule, val, callback) => {
              const target = this.formData.target
              if (!target) {
                callback(new Error('请先选择组件名'))
              } else if (!val) {
                callback(new Error('请选择基础事件名'))
              } else {
                const unionKey = `${target}:${val}`
                const index = this.events.findIndex(
                  (item) => item.eventName === unionKey
                )
                if (index !== -1 && index !== this.editIndex) {
                  callback(new Error(`事件“${unionKey}”已存在`))
                } else {
                  callback()
                }
              }
            },
          },
        ],
        params: [{ required: true }],
      },
      paramsRules: {
        required: true,
        trigger: 'blur',
        validator: (rule, val, callback) => {
          if (!val) {
            callback(new Error('不能为空'))
          } else {
            const result = this.formData.params.filter(
              (item) => item.name === val
            )
            if (result && result.length >= 2) {
              callback(new Error('名字不能相同'))
            } else {
              callback()
            }
          }
        },
      },
      editIndex: -1,
      formData: {
        name: '',
        eventName: '',
        params: [],
        target: '',
        eventKey: ''
      },
      tip1: '组件里$emit触发的eventName\n示例: vm.$emit( eventName, […args] )',
      tip2: '组件里$emit传递的args )\n示例: vm.$emit( eventName, […args] )',
      tip3: 'ANTV G2Plot 事件传递的参数',
      targetOptions,
      eventsOptions
    }
  },
  created() {},
  mounted() {},
  methods: {
    eventKeyChange(value) {
      const { target } = this.formData
      if(target) {
        // 所有选项
        let allOptions = []
        eventsOptions.forEach(item => {
          item.options.forEach(opt => {
            allOptions.push(opt)
          })
        })
        // 生成组合名字
        this.formData.name = `${targetOptions.find(item => item.value === target)?.label}-${allOptions.find(item => item.value === value)?.label}`
        // 生成组合事件名
        this.formData.eventName = `${target}:${value}`
        // 生成固定参数
        this.formData.params = _.cloneDeep([{ key: 'param1', name: '图表事件参数' }])
      }
    },
    open(item) {
      this.editIndex = !item
        ? -1
        : this.events.findIndex((event) => event.eventName === item.eventName)
      if (this.editIndex !== -1) {
        this.formData = _.cloneDeep(item)
      } else {
        this.formData = {
          name: '',
          eventName: '',
          params: [],
        }
      }
      this.visible = true
      this.$nextTick(() => {
        this.$refs.form.clearValidate()
      })
    },
    addParams() {
      const length = this.formData.params.length
      this.formData.params.push({
        key: `param${length + 1}`,
        name: '',
      })
    },
    confirm() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.visible = false
          this.$emit('confirm', _.cloneDeep(this.formData), this.editIndex)
        }
      })
    },
  },

  computed: {
    isAntvChartMode() {
      return 'antv-chart' === this.elementType
    }
  },

  watch: {},
}
</script>
<style lang='less' scoped>
.el-dialog__body::v-deep {
}
.add-btn {
  text-align: center;
  color: #409eff;
  cursor: pointer;
}
</style>